#aboutUs .timeline > li > .timeline-panel                               {   width: 46%;float: left;border: 1px solid #d4d4d4;border-radius: 2px;padding: 20px;position: relative;box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);}
